.box {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.levels {
  width: 100%;
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  border: 2px solid #6f99eb;
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}
.level {
  padding: 13px 0;
  width: 100%;
}
.activeLevel {
  background: #6f99eb;
  border-radius: 30px;
}
.levelText {
  font-family: "Gilroy";
  font-size: 0.75rem;
  line-height: 14px;
  text-transform: uppercase;
  color: white;
  margin: 0;
  opacity: 0.5;
  white-space: nowrap;
  text-align: center;
}
.levelTextActive {
  opacity: 1;
}
.percents {
  width: 100%;
  margin-top: 5px;

  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.percentsWrapper {
  width: 100%;
  position: relative;
}
.percentsText {
  font-family: "Gilroy";
  font-size: 1rem;
  line-height: 19px;
  color: white;
  width: 100%;
  text-align: center;
  opacity: 0.5;
  font-weight: bold;
}
.activePercentText {
  text-shadow: 0px 6.12241px 60px rgba(171, 205, 255, 0.6), 0px 0px 20px #8eb5ef;
  opacity: 1;
}
.arrowWrapper {
  position: absolute;
  width: 100%;
}
.percentArrow {
  width: 0;
  height: 0;

  border-top: 5px solid transparent;
  border-left: 10px solid white;
  border-bottom: 5px solid transparent;

  opacity: 0.5;
  margin: 0 auto;
  transform: rotate(-90deg);
}
.activePercentArrow {
  opacity: 1;
  border-left: 10px solid #ff4d00;
}

.loader {
  display: block;
  margin: 0 auto;
}
