.root {
  margin: 30px auto 0;
  max-width: 600;
}
.title {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 27px;
  text-transform: uppercase;
  color: white;
  margin: 20px 0;

  position: absolute;
  top: 20px;
}
.activeTitle {
  cursor: pointer;
}
.arrow {
  transform: rotate(90deg);
  position: relative;
  top: -3px;
  margin-right: 10px;
}
