.box {
  margin: 35px 0;
}

.buttonWrapper {
  max-width: 300px;
  width: 100%;
  margin: 0 auto;
}
.mainText {
  margin: 0;
  color: white;
  line-height: 21px;
  font-size: 1.125rem;
  font-family: "Gilroy";
}

.boxRoot {
  border: 2px dashed #4835ea;
  background: transparent !important;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.18);

  max-height: 64px;
  margin-top: 25px;
  width: 100%;

  border-radius: 8px;
  padding: 20px 0;
  overflow: hidden;

  &:hover {
    & > span {
      transform: scale(1.1);
    }
  }
}

.boxLabel {
  color: white;
  line-height: 24px;
  font-size: 1.25rem;
  font-family: "Gilroy";

  transition: transform 0.4s;
}
