.root {
  margin-top: 20px;
}
.element {
  margin-top: 10px;
  padding: 0 5px;
}

.hiddenText {
  text-overflow: ellipsis;
  overflow: hidden;
}
.amountCoin {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.coin {
  position: static;
  margin-right: 5px;
}
.statusElement {
  display: inline;
  padding: 0.2em 0.6em 0.3em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25em;

  background: #777;

  width: fit-content;
  height: fit-content;

  margin: 15px auto 0;
}
.statusDone {
  background: #5cb85c;
}

.popUp {
  z-index: 10000000;
  color: black;
  padding: 10px 5px;
  border-radius: 5px;
  margin-top: -10px;
  max-width: 150px;
  text-align: center;
}
