.root {
}
.select {
}

.activeText {
  font-family: "Varela Round", sans-serif;
  font-size: 0.8125rem;
  line-height: 16px;
  color: white;
  opacity: 0.5;

  width: 100%;
  text-align: center;
  margin: 20px 0;
}
.button {
  display: block;
  margin: 20px auto;

  background: transparent !important;
  border: 2px solid #4323bd;

  color: white;
  font-family: "Gilroy";
  line-height: 24px;
  font-size: 1.25rem;
  width: 100%;
  padding: 20px;

  max-width: 300px;
  border-radius: 8px;

  overflow: hidden;
  max-height: 68px;
}

.loader {
  margin: 0 auto;
  margin-top: -10px;
}
