.paper {
  height: fit-content;

  & > div {
    @media (max-width: 600px) {
      height: 100vh;
    }

    & > div {
      background: transparent;

      max-width: 600px !important;
      width: 100% !important;
      height: fit-content;

      @media (max-width: 600px) {
        max-width: 100% !important;
        width: 100% !important;
      }
    }
  }
}
.scrollBox {
  overflow-y: auto;
  margin-right: -10px;
  padding-right: 10px;
}
.root {
  box-shadow: 0px 24px 30px rgba(0, 0, 0, 0.2);
  border-radius: 30px;
  padding: 30px;

  width: 100%;
  min-width: 490px;
  overflow: hidden;
  position: relative;

  display: flex;
  flex-direction: column;

  @media (max-width: 600px) {
    min-width: 320px !important;
    height: 100%;
    padding: 15px;
    border-radius: 0;
  }
}
.bonus {
  min-width: 490px;
  background: linear-gradient(131.49deg, #24375f 2.6%, #000000 60.63%) #0a0d18;
}
.rake {
  background: linear-gradient(142.98deg, #412174 3.73%, #000000 75.49%) #0a0d18;
}
.medal {
  background: linear-gradient(128.93deg, #303d31 3.82%, #000000 64.52%) #0a0d18;
}
