@import "sass/index";

.root {
  // background: rgba(#ffffff, 0.1);
  background: use(--template-statisticsBlock-tab-placeBackground);
  position: relative;
  margin: 10px 0 30px;
  border-radius: 8px;
  width: 100%;
  height: 70px;

  cursor: pointer;
}

.select {
  height: 100%;
  max-height: 70px;
}

.text {
  font-family: "Gilroy";
  color: use(--template-anyContent-colors-first);
  font-size: 1.4375rem;
  line-height: 27px;
}

.selectItem {
  padding: 10px;

  & > img {
    width: 24px;
    height: 24px;
  }
}

.label {
  color: use(--template-anyContent-colors-first);
}
