.redeemWrapper {
  display: flex;
  align-items: flex-end;

  @media (max-width: 450px) {
    flex-wrap: wrap;
  }
}
.redeemInputRoot {
  height: 40px;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  border: 1px dashed rgba(255, 255, 255, 0.1);
  border-radius: 8px;
}
.redeemInput {
  color: white;
  font-size: 1rem;

  &::placeholder {
    color: white;
    font-size: 0.875rem;
    line-height: 17px;
    font-family: "Varela Round", sans-serif;
  }
}
.redeemButtonRoot {
  height: 40px;
  margin: 0;

  margin-left: 20px;
  background: #4835ea !important;

  @media (max-width: 450px) {
    margin-left: 0;
    margin-top: 10px;
  }

  &:hover {
    box-shadow: 0px 0px 13px rgba(142, 181, 239, 55%) !important;
  }
}
.redeemLabel {
  white-space: nowrap;
  padding: 10px 20px;

  font-family: "Gilroy";
  font-size: 0.9375rem;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: white;
}
