.root {
  display: grid;
  margin: 20px 0;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  justify-items: center;
}

.card {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  width: 220px;
  margin: 15px;
  padding: 30px;

  cursor: pointer;

  transition: transform 0.4s;
  &:hover {
    transform: scale(1.1);
  }
}
.cardWrapper {
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.medalImg {
  height: 160px;
  width: 160px;
  object-fit: contain;
}
.medalName {
  line-height: 1.25rem;
  font-family: "Gilroy";
  font-size: 1.25rem;
  text-align: center;
  margin: 20px 0 0;
  color: white;
}
.medalDescription {
  font-family: "Gilroy";
  text-align: center;
  line-height: 1rem;
  color: #ffffff;
  font-size: 1rem;
  margin: 10px 0;
  opacity: 0.7;
}

.numberWrapper {
  position: relative;
  display: flex;
  justify-content: center;
}
.numberImg {
}
.numberElement {
  position: absolute;
  top: -12px;

  font-family: "Gilroy";
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: #a1f069;
}
