.root {
  margin-top: 30px;
  max-width: 500px;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.img {
  width: 100%;
  height: 100%;

  max-width: 300px;
  max-height: 300px;
  margin-bottom: 30px;
}

.title {
  font-family: "Gilroy";
  font-size: 1.75rem;
  line-height: 24px;
  color: white;
  margin: 0;
  margin-bottom: 30px;
  text-align: center;
}

.description {
  color: white;
  margin: 0 0 20px;

  font-size: 1rem;
  line-height: 1rem;
  text-align: center;

  opacity: 0.7;
}

.processWrapper {
  width: 100%;
}

.processNumber {
  font-family: "Gilroy";
  font-size: 1rem;
  line-height: 19px;
  opacity: 0.8;

  text-align: center;
  margin: 0;
  color: #5e617d;
}

.processNumFull {
  color: white;
}

.processUnit {
  position: relative;
  width: 100%;
  height: 10px;
  background: #404253;
  border-radius: 4px;
  padding: 2px;
  margin-top: 10px;
}

.processFull {
  height: 6px;
  border-radius: 3px;
  background: #cafca4;
  position: absolute;

  max-width: 0;
  width: 100%;

  transition: max-width 0.4s;
}

.profitBox {
  width: 100%;
  padding: 15px;

  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.06) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  border: 1px dashed rgba(72, 53, 234, 0.6);
  box-shadow: 0px 0px 100px rgba(72, 53, 234, 0.2) inset;
  border-radius: 8px;

  margin: 30px 0 20px;
}

.profitText {
  margin: 0;
  color: white;
  font-size: 1.125rem;
  line-height: 2rem;
  opacity: 0.5;
  font-family: "Varela Round", sans-serif;
  text-align: center;
}

.userBox {
  width: 100%;
  background: rgba(#ffffff, 0.1);
  border-radius: 8px;

  display: flex;
  justify-content: space-between;
  padding: 15px 25px;

  font-family: "Varela Round", sans-serif;
  font-size: 1.125rem;
  line-height: 2rem;
  color: white;

  margin-bottom: 50px;
}

.userName {
  margin: 0;
  opacity: 0.5;
}

.prize {
  margin: 0;
}

.imgWrapper {
  position: relative;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.imgElement {
}

.imgNumber {
  position: absolute;
  top: -15px;

  font-family: "Gilroy";
  font-size: 0.875rem;
  line-height: 24px;
  color: #a1f069;
}
