.root {
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );
  border: 1px dashed rgba(72, 53, 234, 0.572962);
  border-radius: 8px;
  box-shadow: 0px 0px 24px rgba(72, 53, 234, 0.2);

  display: grid;
  grid-template-columns: 1fr 1fr;

  height: 60px;
  padding: 10px;
  overflow: hidden;

  @media (max-width: 450px) {
    grid-template-columns: 1fr;
    height: fit-content;
  }
}
.buttonWrapper {
  display: flex;
  & > * {
    margin-left: 1px;
    border-radius: 6px;
    background: rgba(#ffffff, 0.1) !important;

    color: rgba(#ffffff, 0.5);
    padding: 10px;
    margin-top: -2.5px;
  }

  @media (max-width: 450px) {
    margin-top: 10px;
    justify-content: flex-end;
  }
}

.inputWrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.input {
  background: transparent;
  border: none;
  outline: none;
  height: 25px;

  font-family: "Varela Round", sans-serif;
  font-size: 1.125rem;
  line-height: 22px;
  color: white;
  width: 100%;
}
.label {
  margin: 0;
  font-family: "Varela Round", sans-serif;
  font-size: 0.8125rem;
  line-height: 16px;
  color: white;
  opacity: 0.5;
  margin-top: -5px;
}
