.root {
  margin-bottom: 40px;
  display: flex;
  align-items: flex-end;
}

.wrapper {
}
.text {
  margin: 0;
  width: 100%;
  text-align: center;

  font-size: 1rem;
  color: white;
  font-weight: bold;
  font-family: "Gilroy";
  text-shadow: 0px 6px 25px rgba(171, 205, 255, 0.6), 0px 0px 20px #8eb5ef;
}
.disableText {
  color: #5e617d;
  text-shadow: none;
}
.img {
  width: 100%;
  max-width: 120px;
  max-height: 8px;
  object-fit: scale-down;

  & path {
    fill: #cafca4;
  }
}
.disableImg {
  & path {
    fill: #404253;
  }
}
