@import "sass/index";

.root {
  display: flex;
  flex-direction: column;

  justify-content: space-between;
  height: 100%;
}
.paginationRoot {
  margin-top: 20px;
}
.paginationButton {
  color: white;
  background: transparent;
  box-shadow: none;
}

.paginationActive {
  opacity: 1 !important;

  color: rgba(0, 0, 0, 0.87);
  background-color: #b78fff !important;
}

.paginationRoot {
  color: white;
}
