.root {
  position: relative;
}

.contentWrapper {
  transition: transform 0.4s;
  transform-origin: 50% 0%;
}
.subTitle {
  font-family: "Varela Round", sans-serif;
  font-size: 1.25rem;
  line-height: 24px;
  color: #b78fff;
  margin: 0;
}
.linkText {
  font-size: 1rem;
  font-weight: bold;
  line-height: 19px;
  text-align: center;

  color: white;
  cursor: pointer;
  font-family: "Gilroy";

  width: fit-content;
  margin: 30px auto;
  display: block;
}

.tableWrapper {
  transition: transform 0.4s;
  transform-origin: 50% 100%;
  transform: scaleY(0);
  position: absolute;
  height: 100%;
  bottom: 0;
}
