.root {
}

.listWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 10px 0;

  @media (max-width: 500px) {
    grid-template-columns: 1fr;
  }
}
.listElement {
  font-family: "Varela Round", sans-serif;
  font-size: 1.125rem;
  line-height: 32px;
  color: white;
  margin: 0;

  white-space: nowrap;
}

.loader {
  width: 100%;
  display: flex;
  justify-content: center;

  height: 100px;
}
